___CSS_LOADER_EXPORT___.push([module.id, `/* Responsive Styling */
@media (max-width: 1200px) {
  .hero-image {
    width: 100vw; /* Full width untuk viewport < 1200px */
  }
}

@media (min-width: 1200px) {
  .hero-image {
    width: 100%; /* Full width tetapi minimal 1000px */
    max-width: 100vw;
    min-width: 1000px; /* Pastikan minimal 1000px di layar besar */
  }

  .hero {
    max-width: 100vw;
    margin: 0 auto; /* Center the hero section */
  }
}

/* Responsive (Hamburger Menu) */
@media (max-width: 768px) {
    .toggle-button {
      display: flex; /* Show hamburger button on small screens */
      margin-right: 20px;
    }

    .navbar-links {
      display: none; /* Hide navbar links on small screens by default */
      position: absolute;
      top: 100%;
      right: 0;
      background-color: #A79277;
      padding: 1rem;
      width: 200px;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
      z-index: 1001;
    }

    .navbar-links.active {
      display: block; /* Show dropdown when the hamburger button is clicked */
    }

    .navbar-links ul {
      flex-direction: column; /* Display links vertically */
    }

    .navbar-links ul li {
      margin: 10px 0;
      border-bottom: 2px solid #ddd; /* Bottom border for each item */
    }

    .navbar-links ul li a:hover {
      color: white; /* Change text color on hover */
      background-color: #987070; /* Change background on hover */
      border-radius: 5px; /* Add a small border radius */
    }
}

/* Responsive Styling for Smaller Screens */
@media (max-width: 768px) {
  .hero-content h1 {
      font-size: 2rem; /* Smaller heading on small screens */
  }

  .hero-content p {
      font-size: 1.2rem; /* Smaller paragraph on small screens */
  }

  .search-bar input {
      width: 80%; /* Full width for search input on smaller screens */
      padding: 8px;
      font-size: 1rem;
  }

  .search-bar button {
      padding: 8px 16px;
      font-size: 1rem;
  }
}

/* Responsive Styling for Tablets */
@media (min-width: 768px) and (max-width: 1024px) {
  .hero-content h1 {
      font-size: 2.5rem; /* Medium heading for tablet */
  }

  .hero-content p {
      font-size: 1.3rem; /* Medium paragraph for tablet */
  }

  .search-bar input {
      width: 350px;
      padding: 10px;
  }

  .search-bar button {
      padding: 10px 18px;
  }
}

/* Responsiveness for Restaurant Container */
@media (max-width: 768px) {
    .restaurant-container {
      grid-template-columns: 1fr; /* One column on small screens */
      padding: 20px; /* Reduce padding on small screens */
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .restaurant-container {
      grid-template-columns: repeat(2, 1fr); /* Two columns on tablets */
      padding: 30px 50px; /* Medium padding for tablets */
    }
}
